body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.breadcrumb {
	margin-top: 10px;
}

.name {
	font-size: 1.1em;
	text-decoration: none !important;
	color: black;
}


.name:hover {
	text-decoration: none !important;
	color: black !important;

}

.breadcrumb a {
	color: #5c5954;
}


.breadcrumb a:hover {
	background-color: #ffefd5;
	color: #5c5954;
}
.active {
	text-decoration: underline !important;
	font-weight: bold;
}
.sectionBox.card {
  padding-top: 10px;
  margin-top: 10px;
  padding-left: 5px;
}


.sectionBox.card:hover {
	background: #007bbf !important;
	color:white;
	text-decoration: none;

	-webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
}


.sectionBox {
	color: black;

}




.sectionBox.card .section-icon {
	font-size: 80px;
}


.sectionBox.card:hover .section-icon{
	color:white;

	font-size: 100px;
	margin-top: -10px !important;
	margin-bottom: -10px !important;

	-webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
}

.sectionBox .section-image {
	width: 90px;
	display: block;
	margin: auto;
	margin-top: 25px;
	margin-bottom: 25px;
}

.sectionBox.card:hover .section-image{
	-webkit-filter: invert(100%);
	        filter: invert(100%);
	width: 110px;
	margin-top: -3px !important;
	margin-bottom: -3px !important;
	
	-webkit-transition: all 0.3s ease;
    -moz-transition: all 0.3s ease;
    -o-transition: all 0.3s ease;
    -ms-transition: all 0.3s ease;
}


.personalInfoBox {
	padding-bottom: 10px !important; 
	padding-top: 10px !important;
}


.contact a {
	color: black;
}

.contactMethods {
	display:block;
	margin:auto;
}


.contactIcon {
	font-size: 30px;
	display:block;
	margin-top: -15px;
	margin-bottom: -10px;


}

.contactText {

	font-size: 0.8em;

}


.avatarImg {
	width: 100px;
	border-radius: 15px;
}

.avatarImgSmall {
	width: 100px;
	display:block;
	margin:auto;
	border-radius: 15px;
	margin-bottom:5px;
}


.title div {
	float: left;
    clear: none; 
}

.wrapper {
  text-align: center;
}
.title {
  display: inline-block;
  margin-bottom: -10px;
}

#avatarDiv {
	margin-right:10px;
}



.projectBox h3 {
	font-size: 1.5em;
	padding: 5px;
}


.projectBox .badge {
	margin-right: 10px;
}

.projectBox .image {
 
    border-radius:5px;
    overflow:hidden;
}


.projectBox .card-footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  max-height: 90px;
  padding-top: 3px !important;
  padding-bottom: 3px !important;
}

.projectBox .card-block {
  margin-top: 20px;
  padding-bottom: 75px;
  padding-left: 10px;
  padding-right: 10px;
}

.card-deck {
	margin-top: 20px;
}

#projectsSubtitle {
  width: 100%;
  font-size: 1.3em;
  margin-bottom: 30px;
  text-align:center;
}

  
#cvImg {
	display:block;
	margin:auto;
	width: 100%;
}

#cvDownload {
	width: 100%;
	font-size: 1.3em;
	margin-bottom: 30px;
	text-align:center;
}



@media (min-width: 992px) {  /*On big screens, reduce width of resume to look better*/
	#cvImg {
		display:block;
		margin:auto;
		width: 80%;
	}

}
#hackathonsText {
	 width: 100%;
 	 font-size: 1.3em;
  	margin-bottom: 30px;
  	text-align:center;
}

#hackathonsTable {
	margin-top: 20px;
}
